h1,h2,h3,h4,h5,h6,p,a,button,input{font-family:"Noto Sans","Segoe UI",Tahoma,Geneva,Verdana,sans-serif;color:#1c1d22}@-webkit-keyframes wiggle{to{-webkit-transform:rotate(0deg);transform:rotate(0deg)}}@keyframes wiggle{to{-webkit-transform:rotate(0deg);transform:rotate(0deg)}}.woops-logo{-webkit-transform-origin:center center;transform-origin:center center;-webkit-transform:rotate(-5deg);transform:rotate(-5deg)}.woops-logo:hover{-webkit-animation:1s ease-in-out 0s infinite alternate wiggle;animation:1s ease-in-out 0s infinite alternate wiggle;text-decoration:underline}.woops-logo-animated{-webkit-transform-origin:center center;transform-origin:center center;-webkit-transform:rotate(-5deg);transform:rotate(-5deg);-webkit-animation:1.5s ease-in-out 0s infinite alternate wiggle;animation:1.5s ease-in-out 0s infinite alternate wiggle}
*{box-sizing:border-box}html,body,#root{width:100%;height:100%}#root{display:flex;flex-direction:column;align-items:center}body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}main{width:100%;flex-grow:1;display:flex;flex-direction:column}
header{width:100%;background-color:#cc4846;padding:.5rem 1rem}@media(min-width: 37.5em){header{padding:1rem}}header nav{display:flex;justify-content:space-between}header a,header button{display:flex;align-items:center;text-decoration:none;color:#fff;font-weight:bold;cursor:pointer}header .woops-logo{font-size:2rem}@media(min-width: 37.5em){header .woops-logo{font-size:1.5rem}}header .acc-links{display:flex;flex-direction:column}@media(min-width: 37.5em){header .acc-links{flex-direction:row}}header .icon{margin-right:.2rem;height:1.2rem;width:1.2rem}header .profile-btn{margin-bottom:.25rem}@media(min-width: 37.5em){header .profile-btn{margin-right:1.5rem;margin-bottom:0}}header .logout-btn{color:#fff;border:none;background-color:transparent;margin:0;padding:0}header .profile-btn,header .logout-btn{transition-property:background-color,color,-webkit-text-decoration;transition-property:text-decoration,background-color,color;transition-property:text-decoration,background-color,color,-webkit-text-decoration;transition-duration:.25s;padding:.5rem;border-radius:.5rem;border:2px solid transparent}header .profile-btn:hover,header .logout-btn:hover{text-decoration:underline;background-color:#7cfef0;color:#1c1d22;border:2px solid #1c1d22}
