.login-background{display:flex;justify-content:center;width:100%;height:100%;background-color:#cc4846}.login-wrapper{max-width:112rem;width:100%;padding:1.5rem;display:flex;flex-direction:column}@media(min-width: 37.5em){.login-wrapper{flex-direction:row;align-items:stretch;min-height:70%;margin:auto}}@media(min-width: 56.25em){.login-wrapper{padding-left:5rem;padding-right:5rem}}@media(min-width: 75em){.login-wrapper{padding-left:13rem;padding-right:13rem;min-height:60%}}.login-text{padding:1rem;padding-right:2rem;font-size:1.5em}@media(min-width: 37.5em){.login-text{flex-grow:2}}@media(min-width: 56.25em){.login-text{flex-grow:4;font-size:2em}}@media(min-width: 75em){.login-text{font-size:2.5em}}.login-text h1,.login-text p{color:#fff}.login-text h1{font-size:2em;margin-top:1rem;margin-bottom:.5rem}.login-text p{font-size:1em;margin-bottom:2rem}.login-form{flex-grow:1;background-color:#fff;padding:2rem;border-radius:.5rem}@media(min-width: 37.5em){.login-form{max-width:25rem;flex-grow:1;padding-left:3rem;padding-right:3rem}}.login-form div{display:flex;align-items:center;justify-content:center;flex-wrap:wrap;margin-top:1rem;margin-bottom:1rem}.login-form p,.login-form button{padding:0;margin:0;display:inline-block;white-space:nowrap;margin-top:.2rem}.login-form p{margin-right:.5rem}.login-form button{border:none;background-color:transparent;color:#1d725f;text-decoration:underline;transition:background-color .5s}.login-form button:hover{cursor:pointer;text-decoration:none;background-color:#7cfef0}.login-form form{display:flex;flex-direction:column}
label{display:block;margin-bottom:.25rem;font-size:.9em}input{display:block;margin-bottom:.75rem;border:2px solid #1c1d22;border-radius:.25rem;padding:.25rem}input[type=submit]{margin-top:1.5rem;background-color:#1c1d22;color:#fff;font-weight:bold;padding:.5rem;transition-property:color,background-color;transition-duration:.5s}input[type=submit]:hover{color:#1c1d22;background-color:#7cfef0;cursor:pointer}
